.header{
    z-index: 5;
    height: 54px;
    width: 100%;
    background: #edeef0;
    grid-area: header;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    border-bottom: 1px solid rgb(222, 217, 217);
    font-size: 24px;
}
.content{
    height: 50px;
    width: 990px;
    display: flex;
    justify-content: space-between;
}
.logo{
    padding-top: 6px;
    width: 140px;
    height: 42px;
}
.login{
    padding: 15px 0 0 0 ;
    cursor: pointer;
}
.logout{
    padding-left: 7px;
}
.logout img{
    width: 18px;
    height: 18px;
}
.loginBlock{
    display: flex;

}
